import React, { lazy, Suspense, useState } from "react";
import { Dialog, Button, EyeOpenIcon, Pill } from 'evergreen-ui';

import skills from '../../../stores/SkillsStore';

import Spinner from '../../common/Spinner';
// import SkillsGrid from './Skills';


const SkillsGrid = lazy(() => import("./Skills"))


function SkillPane() {
  const [ isShown, setIsShown ] = useState(false)

  return (
    <React.Fragment>
      <Dialog
        isShown={isShown}
        title="My Skills"
        onCloseComplete={() => setIsShown(false)}
        hasFooter={false}
        height="80%"
        width="75%"
      >
        <Suspense fallback={<Spinner />}>
          <SkillsGrid skills={skills}/>
        </Suspense>

      </Dialog>
      <Button
        style={{color: "black"}}
        height={32}
        intent="none"
        iconBefore={EyeOpenIcon}
        onClick={() => setIsShown(true)}
        margin={8}
      >
        Skills <Pill display="inline-flex" marginLeft={8}>{skills.length}</Pill>
      </Button>
    </React.Fragment>
  )
}

export default SkillPane