import React from "react";
import { Pane } from 'evergreen-ui';

// Custom components
import Badge from './Badge';
import badges from '../../../stores/BadgesStore';


export default () => (
    <Pane margin={4}>
      {
        badges.map(
          badge => badge.link ? <Badge key={badge.alt} {...badge} /> : <br />
        )
      }
    </Pane>
)
